<template>
  <section class="data_section" ref="data_section">
    <h3 class="aTitle">生存能力分析</h3>

    <div class="headerData">
      <el-select v-model="data.uid" @change="selectGet" filterable placeholder="请选择人员">
        <el-option
          v-for="item in options"
          v-if="item.mode !='1'"
          :key="item._id"
          :label="item.name"
          :value="item._id"
        ></el-option>
      </el-select>
      <el-date-picker
        class="dateWrapper"
        v-model="value1"
        type="datetimerange"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
      ></el-date-picker>
      <el-button type="primary" class="search" @click="search(4)" icon="el-icon-search">查询</el-button>
    </div>

    <el-row :gutter="10" class="row_list order_list">
      <el-col :span="12" class="searchWrapper">
        <el-button class="but1 search" type="primary" @click="search(2)">总体数据</el-button>
        <el-button type="primary" class="search" @click="search(0)">前仓数据</el-button>
        <el-button type="primary" class="search" @click="search(1)">后仓数据</el-button>
      </el-col>
      <el-col :span="6">&nbsp</el-col>
      <el-col :span="6">
        <ul class="df">
          <!-- <li>
            <p>生存时间：{{time}}</p>
            <el-progress :stroke-width="20" :percentage="livepercent" :format="format"></el-progress>
          </li>-->
          <li>
            <p>生存时间占比</p>
            <el-progress :stroke-width="20" :percentage="livepercent" :format="format2"></el-progress>
          </li>
        </ul>
      </el-col>
    </el-row>
    <el-row :gutter="10" class="row_list order_list">
      <div class="aTopLeft fTitle">
        <h1>生存率</h1>
        <ul>
          <li>
            <span></span>
            <p>规避</p>
            <p>{{(hidepercent*100).toFixed(2)}}%</p>
            <!-- <p>{{gl.totle.mzcs}}({{gl.totle.mzgl}})</p> -->
          </li>
          <li>
            <span></span>
          </li>
          <li>
            <span></span>
            <p>未规避</p>
            <p>{{(behitpercent*100).toFixed(2)}}%</p>
            <!-- <p>{{gl.totle.wmzcs}}({{gl.totle.wmzgl}})</p> -->
          </li>
        </ul>
      </div>
      <div class="aTopRight aButtomRight" id="aButtomRight">
        <ul class="aRightTJ">
          <h1>被命中概率</h1>
          <li>
            <span id="hpWrapper"></span>
            <p>航炮</p>
            <div>
              <el-progress :stroke-width="20" :percentage="gl[255].mzgl" :format="format"></el-progress>
              <!-- <el-progress :stroke-width="20" :percentage="gl[255].wmzgl" :format="format2"></el-progress> -->
            </div>
          </li>
          <li>
            <span></span>
            <p>P-10</p>
            <div>
              <el-progress :stroke-width="20" :percentage="gl[10].mzgl" :format="format"></el-progress>
              <!-- <el-progress :stroke-width="20" :percentage="gl[10].wmzgl" :format="format2"></el-progress> -->
            </div>
          </li>
          <li>
            <span></span>
            <p>P-12</p>
            <div>
              <el-progress :stroke-width="20" :percentage="gl[12].mzgl" :format="format"></el-progress>
              <!-- <el-progress :stroke-width="20" :percentage="gl[12].wmzgl" :format="format2"></el-progress> -->
            </div>
          </li>
          <li>
            <span></span>
            <p>P-15</p>
            <div>
              <el-progress :stroke-width="20" :percentage="gl[15].mzgl" :format="format"></el-progress>
              <!-- <el-progress :stroke-width="20" :percentage="gl[15].wmzgl" :format="format2"></el-progress> -->
            </div>
          </li>
        </ul>
      </div>
    </el-row>
    <el-row :gutter="10" class="row_list order_list">
      <div class="aTopLeft" id="aTopLeft">
        <bottomPie1 ref="bottomPie1"></bottomPie1>
        <bottomPie2 ref="bottomPie2"></bottomPie2>
        <bottomPie3 ref="bottomPie3"></bottomPie3>
      </div>
    </el-row>
  </section>
</template>

<script>
import { fAnalysis } from "@/api/f";
import bottomPie1 from "./componentsB/bottomPie1";
import bottomPie2 from "./componentsB/bottomPie2";
import bottomPie3 from "./componentsB/bottomPie3";
import bottomPie4 from "./componentsB/bottomPie4";
export default {
  components: {
    bottomPie1,
    bottomPie2,
    bottomPie3,
    bottomPie4
  },
  data() {
    return {
      value1: "",
      hidepercent: null,
      behitpercent: null,
      data: {
        uid: "",
        name: "",
        site: "2",
        start: "",
        end: ""
      },
      user: this.sessionData("get", "user"),
      options: this.sessionData("get", "userList"),
      time: "",
      livepercent: 0,
      gl: {
        255: {
          mzgl: 0,
          wmzgl: 0
        },
        10: {
          mzgl: 0,
          wmzgl: 0
        },
        12: {
          mzgl: 0,
          wmzgl: 0
        },
        15: {
          mzgl: 0,
          wmzgl: 0
        },
        totle: {
          mzgl: 0,
          mzglcs: 0,
          wmzgl: 0,
          wmzglcs: 0
        }
      }
    };
  },
  created() {},
  mounted() {
    this.fAnalysis(this.user.name, this.data.site);
  },
  methods: {
    search(s) {
      s = s == 4 ? this.data.site : s;
      var st = this.value1 ? this.value1[0].getTime() : "";
      var et = this.value1 ? this.value1[1].getTime() : "";
      this.fAnalysis(this.data.name, s, st, et);
    },

    selectGet(vId) {
      let obj = {};
      obj = this.options.find(item => {
        return item._id === vId;
      });
      this.data.name = obj.name;
    },
    fAnalysis(name, site, start, end) {
      fAnalysis({
        name: name || this.user.name,
        site: site,
        start: start || "",
        end: end || ""
      }).then(res => {
        if (res.code == 0) {
          this.$message({
            message: "数据查询成功！",
            type: "success"
          });
          var list = this.$unfly(res.data);
          this.hidepercent = list.hidepercent;
          this.behitpercent = list.behitpercent;
          this.time = (list.livetime / 1000 / 60 / 60).toFixed(2);
          try {
            this.livepercent = Number((list.livepercent*100).toFixed(2));
            //this.livepercent = Number((list.livepercent/list.time*100).toFixed(2));
             
          } catch (error) {}

          console.log("f能力数据分析", list);

          this.setHit(list.behit, list.hide);
          this.$refs.bottomPie1.init(list.cause[10]);
          this.$refs.bottomPie2.init(list.cause[12]);
          this.$refs.bottomPie3.init(list.cause[15]);
        }
      });
    },

    setHit(mz, wmz) {
      var obj = {};
      var mzglcs = 0;
      var wmzglcs = 0;
      for (var i in mz) {
        obj[i] = [mz[i], wmz[i]];
        mzglcs += mz[i];
        wmzglcs += wmz[i];
      }

      this.gl.totle.mzgl = isNaN(mzglcs / (mzglcs + wmzglcs))
        ? Number(0)
        : Number((mzglcs / (mzglcs + wmzglcs)).toFixed(0));
      this.gl.totle.wmzgl = isNaN(wmzglcs / (mzglcs + wmzglcs))
        ? Number(0)
        : Number((wmzglcs / (mzglcs + wmzglcs)).toFixed(0));
      this.gl.totle.mzglcs = mzglcs;
      this.gl.totle.wmzglcs = wmzglcs;

      for (var i in obj) {
        this.gl[i].mzgl = isNaN(obj[i][0] / (obj[i][0] + obj[i][1]))
          ? Number(0)
          : Number(((obj[i][0] / (obj[i][0] + obj[i][1]))*100).toFixed(0));
        this.gl[i].wmzgl = isNaN(obj[i][1] / (obj[i][0] + obj[i][1]))
          ? Number(0)
          : Number(((obj[i][1] / (obj[i][0] + obj[i][1]))*100).toFixed(0));
      }
      console.log("查看命中概率", this.gl);
    },

    format(percentage) {
      return "被命中概率" + percentage + "%";
    },
    format2(percentage) {
      return percentage + "%";
    }
  }
};
</script>

