<template>
  <div id="bottomPie4"></div>
</template>

<script>
import echarts from "echarts";
export default {
  data() {
    return {
      myChart: null
    };
  },
  mounted() {
    this.loadChart();
  },

  methods: {
    loadChart() {
      this.myChart = echarts.init(document.getElementById("bottomPie4"));
      this.myChart.setOption(this.initOption());
    },
    initOption() {
      return {
    title: {
        text: '航炮未命中原因',
        subtext: '',
        left: 'center'
    },
    tooltip: {
        trigger: 'item',
        formatter: '{a} <br/>{b} : {c} ({d}%)'
    },
    legend: {
        bottom: 10,
        left: 'center',
        data: ['原因1', '原因2', '原因3', '原因4']
    },
    color: ["#ffc000", "#a5a5a5","#ed7d31", "#5b9bd5"],
    series: [
        {
            name: '访问来源',
            type: 'pie',
            radius: '55%',
            center: ['50%', '50%'],
            data: [
                {value: 335, name: '原因1'},
                {value: 310, name: '原因2'},
                {value: 234, name: '原因3'},
                {value: 135, name: '原因4'},
            ],
            emphasis: {
                itemStyle: {
                    shadowBlur: 10,
                    shadowOffsetX: 0,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
            }
        }
      ]
      };
    }
  }
};
</script>
<style lang="less">
#bottomPie4 {
  width: 100%;
  height: 100%;
}
</style>
